import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Category } from '../models/category.model';

@Injectable({
  providedIn: 'root'
})
export class CategoriesService {
  oRes = '';
  formData: Category = new Category;
  readonly rootURL = 'https://localhost:44385/api'
  constructor(private http: HttpClient) { }

  DelCategory(cat_id: string){
    return this.http.delete(this.rootURL + '/Categories/DelCategory/'+cat_id);
  }
  // postCategory(formData: Category) {
  //   return this.http.post(this.rootURL + '/Categories/register', formData);
  // }
  addCategory(formData: Category) {
    return this.http.post(this.rootURL + '/Categories/addCategory', formData);
  }
  editCategory(formData:Category, cat_id: string){
    return this.http.post(this.rootURL + '/Categories/edit/'+ cat_id, formData);
  }
  
  list() {
    return this.http.get(this.rootURL + '/Categories/GetCategories')
  }
  view(cat_id: string) {
    return this.http.get(this.rootURL + '/Categories/GetCategory/' + cat_id)
  }
  clearFormData() {
    this.formData = {
      nombre: "",
      portada: false,
      hide: false,

    };
  }
}
